import { dedupingMixin } from '@bbva-web-components/bbva-core-lit-helpers/utils/mixin.js';
import { randomID } from '@bbva-web-components/bbva-core-lit-helpers/utils/randomId.js';

/**
`BbvaCoreFormAssociationMixin` allows to detect if a custom element is associated to a `<form>` tag. The association could be through the `form` attribute (passing the ID of the form) or just by being inside the form tag.

Association will be checked on `connectedCallback` and when the `form` property changes. If the association exists, the protected property `_formAssociated` will be set to the matching form node. Also, the mixin will check for an inner node with a random ID equal to `_formElementId` (the selector can be overriden using `_formElementSelector` getter). If found, it will move it outside of the custom element shadow root, just before the custom element tag. Thus, that node would be able to communicate with the form. This is useful, for example, for binding custom element properties with properties of the moved node, so allowing the custom element to pass data to forms.

The moved node will be removed when the custom element is disconnected.

```js
import { LitElement, html } from 'lit-element';
import { ifDefined } from 'lit-html/directives/if-defined.js';
import { BbvaCoreFormAssociationMixin } from '@bbva-web-components/bbva-core-form-association-mixin';

class DemoComponent extends BbvaCoreFormAssociationMixin(LitElement) {
  static get properties() {
    return {
      value: {
        type: String
      }
    };
  }

  constructor() {
    super();
    this.value = '';
  }

  render() {
    return html`
      ${this._formElementTpl}

      <div class="inner-input">
        <label>
          Inner input
          <input type="text" .value="${this.value}" @input="${this._onInput}">
        </label>
      </div>
    `;
  }

  get _formElementTpl() {
    return html`
      <div id="${this._formElementId}">
        <input type="hidden" form="${ifDefined(this.form)}" name="demoComponentInput" value="${this.value}">
      </div>
    `;
  }

  _onInput(ev) {
    this.value = ev.target.value;
  }
}
```
 */
export const BbvaCoreFormAssociationMixin = dedupingMixin(
  Base =>
    class BbvaCoreFormAssociation extends Base {
      static get properties() {
        return {
          /**
           * Associated form for control
           */
          form: {
            type: String,
          },

          _formAssociated: {
            type: Boolean,
            attribute: false,
          },

          _formElementId: {
            type: String,
            attribute: false,
          },
        };
      }

      constructor() {
        super();
        this._formElementId = randomID();
        this.__formCheck = document.createElement('button');
        this.__formCheck.style.display = 'none';
      }

      connectedCallback() {
        super.connectedCallback && super.connectedCallback();
        this._initFormCheck();
      }

      disconnectedCallback() {
        if (this._formElement) {
          this._formElement.parentNode.removeChild(this._formElement);
          this._formElement = null;
          this._formAssociated = null;
        }
        super.disconnectedCallback && super.disconnectedCallback();
      }

      _initFormCheck() {
        if (this.form) {
          this.__formCheck.setAttribute('form', this.form);
        } else {
          this.__formCheck.removeAttribute('form');
        }

        this.parentNode.appendChild(this.__formCheck);
        this.updateComplete.then(() => {
          if (this.__formCheck.parentNode) {
            this._formAssociated = this.__formCheck.form;
            this.__formCheck.parentNode.removeChild(this.__formCheck);
          }
        });
      }

      get _formElementSelector() {
        return this.shadowRoot.querySelector(`#${this._formElementId}`);
      }

      updated(props) {
        super.updated && super.updated(props);
        if (props.has('_formAssociated')) {
          this._manageFormAssociatedElement();
        }

        if (props.has('form')) {
          this._initFormCheck();
        }
      }

      _manageFormAssociatedElement() {
        if (this._formAssociated && !this._formElement) {
          this._formElement = this._formElementSelector;
          if (this._formElement) {
            this.parentNode.insertBefore(this._formElement, this);
          }
        }
        if (!this._formAssociated && this._formElement) {
          this._formElement.parentNode.removeChild(this._formElement);
          this._formElement = null;
        }
      }
    },
);
