import { LitElement, html, css } from 'lit-element';
/**
![LitElement component](https://img.shields.io/badge/litElement-component-blue.svg)

`<bbva-core-heading>` provides heading semantics and an easy way to customize and unify heading styles used across a website or application.

Example:

```html
<bbva-core-heading>Heading</bbva-core-heading>
```

You can provide information about the heading level relative to the document by setting the `level` attribute with a numeric value from 1 to 6. By default the heading level (`aria-level`) is set to 2.
If an invalid value is provided, the component won't be treated as a heading.

Example:

```html
<bbva-core-heading level="3">Heading</bbva-core-heading>
```

@customElement bbva-core-heading
*/
export class BbvaCoreHeading extends LitElement {
  static get is() {
    return 'bbva-core-heading';
  }

  static get properties() {
    return {
      /**
       * Heading level from 1 to 6
       */
      level: {
        type: Number,
      },
    };
  }

  constructor() {
    super();
    this.level = 2;
  }

  set level(val) {
    const oldVal = this.__level;
    this.__level = val;

    if (val >= 1 && val <= 6) {
      this.setAttribute('aria-level', val);
      this.setAttribute('role', 'heading');
    } else {
      this.removeAttribute('aria-level');
      this.removeAttribute('role');
    }

    this.requestUpdate('level', oldVal);
  }

  get level() {
    return this.__level;
  }

  static get styles() {
    return css`
      :host {
        display: block;
        box-sizing: border-box;
      }

      :host([hidden]) {
        display: none !important;
      }
    `;
  }

  render() {
    return html` <slot></slot> `;
  }
}
