/* eslint-disable no-console */
import { BbvaCoreMeta } from '@bbva-web-components/bbva-core-meta';
import { css, unsafeCSS } from 'lit-element';
import '../elements/bbva-core-custom-style.js';

if (!window.ShadyCSS || !window.ShadyCSS.ScopingShim) {
  console.warn('ScopingShim not detected. Be sure to add ShadyCSS ScopingShim to your app!');
}

export const setComponentSharedStyles = (id, styles) => {
  if (!styles.cssText) {
    throw new Error(
      'Value passed to "setComponentSharedStyles" function must be a "css" function result',
    );
  }
  const meta = new BbvaCoreMeta({
    type: 'sharedStyles',
    key: id,
  });
  meta.value = meta.value || [];
  meta.value.push(styles);
};

export const setDocumentCustomStyles = (styles, id) => {
  const customStyles = document.createElement('bbva-core-custom-style');
  if (id) {
    customStyles.id = id;
  }
  customStyles.innerHTML = `
    <style>
      ${styles.cssText}
    </style>
  `;
  document.head.appendChild(customStyles);
  return customStyles;
};

/**
 * Function for merging dark mode styles objects in a string
 * @param  {...Object} darkModeStyles Objects
 * @returns
 */
export const composeDarkModeStyles = (...darkModeStyles) => {
  const result = darkModeStyles.reduce(
    (prevValue, currentValue) => ({ ...prevValue, ...currentValue }),
    {},
  );
  return Object.values(result).join(' ');
};

/**
 * Function for generating components dark mode block in theme.
 * Merges all provided dark mode styling objects, thus deduping them, then interpolates them in a 'prefers-color-scheme: dark' media query and puts them at document level using the `setDocumentCustomStyles` helper.
 * @param {Array} darkModeStyles Array of dark mode styles objects
 * @param {String} selector CSS selector for dark mode styles declaration
 * @param {String|Boolean} mediaQuery Can be true for using the default prefers color scheme media query; or a string with a custom media query
 */
export const setDocumentComponentsDarkModeStyles = (
  darkModeStyles,
  selector = ':root[color-scheme-dark]',
  mediaQuery = '',
) => {
  const defaultMediaQuery = '@media (prefers-color-scheme: dark)';
  const result = composeDarkModeStyles(...darkModeStyles);
  let styles = `
    ${selector} {
      ${result}
    }
  `;
  const mediaQueryDeclarationAt =
    typeof mediaQuery === 'boolean' && mediaQuery ? defaultMediaQuery : mediaQuery;
  if (mediaQueryDeclarationAt) {
    styles = `${mediaQueryDeclarationAt} { ${styles} } `;
  }
  setDocumentCustomStyles(
    css`
      ${unsafeCSS(styles)}
    `,
  );
};
